<template>
  <chart :options="option" />
</template>

<script>
  import ECharts from 'vue-echarts/components/ECharts'
  import echartsGl from 'echarts-gl'
  import CommonWrapper from '@/components/commonWrapper'

  import 'echarts/lib/chart/line'
  import 'echarts/lib/component/polar'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/title'
const color = ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026'] ;
  export default {
    name: 'charts3D',
    data () {
      return {
        option: {
          tooltip: {},
          visualMap: {
              max: 20,
              inRange: {
                  color:color
              }
          },
          xAxis3D: {
              type: 'category',
              data: []
          },
          yAxis3D: {
              type: 'category',
              data: []
          },
          zAxis3D: {
              type: 'value'
          },
          grid3D: {
              boxWidth: 200,
              boxDepth: 80,
              viewControl: {
                  // projection: 'orthographic'
              },
              light: {
                  main: {
                      intensity: 1.2,
                      shadow: true
                  },
                  ambient: {
                      intensity: 0.3
                  }
              }
          },
          series: [{
              type: 'bar3D',
              data: [],
              shading: 'lambert',

              label: {
                  textStyle: {
                      fontSize: 16,
                      borderWidth: 1
                  }
              },

              emphasis: {
                  label: {
                      textStyle: {
                          fontSize: 20,
                          color: '#900'
                      }
                  },
                  itemStyle: {
                      color: '#900'
                  }
              }
          }]
}
      }
    },
    props: {
      toxAxis3D:{
        type: Array
      },
      toyAxis3D:{
        type:Array
      },
      toseriesData:{
        type:Array
      },
      rangeColor:{
        type:Array
      }
    },
    watch: {
      toxAxis3D(newVal) {
        if(newVal){
          this.option.xAxis3D.data = newVal
        }
      },
      toyAxis3D(newVal) {
        if(newVal){
          this.option.yAxis3D.data = newVal
        }
      },
      toseriesData(newVal){
        if(newVal){
          this.option.series[0].data = newVal ;
        }
      },
      rangeColor(newVal){
        if(newVal){
          this.option.visualMap.inRange.color = newVal ;
        }
      }

    },
    components: { CommonWrapper, chart: ECharts }
  }
</script>

<style scoped>

</style>



